





































































import Vue from 'vue'
import { getAllSpaces, saveOrUpdateAdSpace } from '@/services/advert'
import dayjs from 'dayjs'
export default Vue.extend({
  data() {
    return {
      adverts: [],
      loading: false,
      dialogVisible: false,
      currentAdSpace: {}
    }
  },
  filters: {
    formatDate(time: any) {
      return dayjs(time).format('YYYY-MM-DD HH:mm')
    }
  },
  created() {
    this.loadAdList()
  },
  methods: {
    async loadAdList() {
      this.loading = true
      const { data } = await getAllSpaces({
        // pageNum: 1,
        // pageSize: 10
      })
      this.loading = false
      this.adverts = data.content
    },
    async addAdSpace() {
      const { data } = await saveOrUpdateAdSpace({
        ...this.currentAdSpace
      })
      this.$message.success(`${(this.currentAdSpace as any).id ? '修改' : '新增'}成功`)
      this.loadAdList()
      this.dialogVisible = false
    },
    gotoEdit(row: any) {
      this.dialogVisible = true
      if (row) this.currentAdSpace = { ...row }
      else {
        this.currentAdSpace = {
          name: '',
          spaceKey: ''
        }
      }
    },
    calcelEdit() {
      this.currentAdSpace = {}
      this.dialogVisible = false
    }
  }
})
